import React from "react"

const WEDDING_HALL_POSITION: [number, number] = [104.8379652, 11.4980323]
const GOOGLE_MAPS_URL =
  "https://www.google.com/maps?q=FRWR+XG+VannaraHour,+Phnom+Penh&output=embed"

const EmbeddedMap: React.FC = () => {
  return (
    <div className="map-wrapper" style={{ width: "100%", height: "400px" }}>
      <iframe
        src={GOOGLE_MAPS_URL}
        style={{ width: "100%", height: "100%", border: "0" }}
        allowFullScreen
        loading="lazy"
        title="Wedding Hall Location"
      ></iframe>
    </div>
  )
}

const NavigationButtons: React.FC = () => {
  const handleGoogleMaps = () => {
    window.open(
      "https://www.google.com/maps?q=FRWR+XG+VannaraHour,+Phnom+Penh",
      "_blank",
    )
  }

  return (
    <div
      className="navigation"
      style={{
        display: "flex",
        gap: "10px",
        marginTop: "10px",
      }}
    >
      <button
        onClick={handleGoogleMaps}
        style={buttonStyle}
        aria-label="Open location in Google Maps"
      >
        <img
          src="data:image/svg+xml;base64,"
          alt="Google Maps Icon"
          style={{ width: "24px", height: "24px", marginRight: "8px" }}
        />
        Google Maps
      </button>
    </div>
  )
}

const buttonStyle: React.CSSProperties = {
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  padding: "8px 12px",
  backgroundColor: "#f0f0f0",
  border: "1px solid #c2a11d",
  borderRadius: "4px",
  cursor: "pointer",
  fontSize: "14px",
  color: "#c2a11d",
  fontWeight: "bold",
  transition: "background-color 0.3s ease",
}

export const Map: React.FC = () => {
  return (
    <>
      <EmbeddedMap />
      <NavigationButtons />
    </>
  )
}
